import React, {Component, PropTypes } from 'react'
//import './Image.css'

class Image extends Component {

    constructor(props) {
        super(props);
        this.state = {src: props.src};
    }

    getStyle() {
        return (
            {
                width: "100%",
                opacity: 0.02 ,
            }
        )
    }
//className={(this.state.hover || location.pathname === this.props.link) ? "PageButton-hover" : "PageButton"}*/
    render() {
        const ImageSource = require('./img1.jpg');
// vstyle={this.getStyle()}>
        return (
                <img src={ImageSource} 
                     alt="Ting"
                     style={this.getStyle()}>
                </img>
        );
    }
    
}

export default Image



